@media (max-width:1280px) {
    .jumbotron {
        margin: 20px 0 0 0;

        &__text {
            table {
                border-spacing: 100px 0;
            }
        }
    }
}

@media (max-width:960px) {
    .jumbotron {
        &__text {
            table {
                border-spacing: 50px 0;
            }
        }
    }
}

@media (max-width:640px) {
    .jumbotron {
        grid-template-columns: 1fr;

        &__text {
            table {
                border-spacing: 50px 0;
            }
        }
    }
}